import React, { useState, useContext, useCallback } from 'react'
import {Link} from 'react-router-dom';
import styled from 'styled-components'
import {
  Card,
  CardBody,
  Heading,
  Tag,
  Button,
  ChevronUpIcon,
  ChevronDownIcon,
  Text,
  CardFooter,
  useModal,
  LogoIcon,
} from '@pancakeswap-libs/uikit'
import { useWallet } from '@binance-chain/bsc-use-wallet'
import { List } from 'antd'
import useI18n from 'hooks/useI18n'
import { SellableNft } from 'config/constants/types'
import { AMOUNT_TO_CLAIM } from 'config/constants/newnfts'
import Page from 'components/layout/Page'
import { NftProviderContext } from '../../contexts/NftProvider'
import { getNftContract } from '../../utils/contracts'


interface NftCardProps {
  nft: SellableNft,
  currentTab: string
}


const NftCard: React.FC<NftCardProps> = ({ nft, currentTab }) => {
  console.log("nft..............", nft);
  const [state, setState] = useState({
    isLoading: false,
    isOpen: false,
    nftCount: 0,
    nftBurnCount: 0,
  })

  console.log("state...", state.isOpen);

  const { account } = useWallet()



  const handleClick = () => {
    console.log("state", state.isOpen);
    if (state.isOpen) {
        setState((prevState) => ({ ...prevState, isOpen: !prevState.isOpen }))
    } else {
        setState((prevState) => ({ ...prevState, isOpen: !prevState.isOpen }))
    }
  }



  return (
        <div className="cblock">
        <img src={`/images/nfts/${nft.previewImage}`} alt={nft.name} className="img-fluid" />
        <div className="cblock-content">
          <div className="cblock-headwrap">
            <div className="cblock-lefthead">
              <h6 className="inner-title">{nft.name}</h6>
              <p>{nft.authorName}</p>
            </div>
            {currentTab === 'owned' && 
            (
              <div className="owdetails-head">
                <i>
                  <img src="/assets/images/arrow-downsm.svg" className={`arrow-icon ${state.isOpen ? 'active' : ''}`} alt="arrow" />
                </i>
                <span className="details-text" onClick={handleClick} onKeyDown={handleClick} role="presentation"> Details </span>
              </div>
            )
            }
            
            {state.isOpen && (
              <div className="details-answer-new">
                {nft.description}
              </div>
            )}
            
          </div>
        </div>
      </div>
  )
}

export default NftCard
